.Result {
    text-align: center;
}

.Result-header {
    background-color: #f6f6e9;
    min-height: 96vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #282c34;
    h1 {
        margin-top: 40px;
        margin-bottom: 55px;
    }
    h3 {
        margin-top: 20px;
        margin-bottom: 15px;
        span {
            color: rgb(12, 78, 139);
        }
    }
}

.Result-btn {
    margin-top: 40px;
    font-size: calc(3vmin);
    padding: 5px 20px;
    border-radius: 25px;
    background-color: rgb(12, 78, 139);
    color: white;
    &:hover {
        background-color: rgb(2, 54, 100);
    }
    &:focus {
        border: none;
        outline: none;
    }
}